.main-nav {
  position: relative;
  width: 100%;
}
@media (max-width: 767px) {
  .main-nav__toggle {
    display: block;
    position: absolute;
    width: 24px;
    height: 18px;
    top: -43px;
    right: calc(50% - 140px);
    border: none;
    background-color: #ffffff;
  }
}
@media (min-width: 768px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (min-width: 1440px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 24px;
    height: 2px;
    background-color: #000000;
    box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__toggle {
    height: 24px;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    width: 25px;
    height: 2px;
    background-color: #000000;
    top: 8px;
    left: 0;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  z-index: 1;
}
@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    flex-wrap: wrap;
    width: 467px;
    margin-left: auto;
    justify-content: flex-end;
  }
}
@media (min-width: 1440px) {
  .main-nav__list {
    width: 479px;
  }
}
@media (max-width: 767px) {
  .main-nav--opened .main-nav__list {
    position: absolute;
    background-color: #ffffff;
    width: 100%;
  }
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--nojs .main-nav__list {
  display: block;
  position: static;
}
